import BaseContent from '../../components/BaseContent/BaseContent'
import jsonView from 'vue-json-views'

export default {
  name: 'menu-3',
  components: {
    BaseContent,
    jsonView
  },
  data () {
    return {
      menuList: {
        path: '/menu-1',
        name: 'menu-1',
        meta: {
          title: '我是一级'
        },
        component: 'layout',
        children: [
          {
            path: 'menu-2',
            name: 'menu-2',
            meta: {
              title: '我是二级'
            },
            component: 'layout',
            children: [
              {
                path: 'menu-3',
                name: 'menu-3',
                meta: {
                  title: '我是三级'
                },
                component: '() => import(' + ')'
              }
            ]
          }
        ]
      }
    }
  }
}
